import { createRouter, createWebHistory } from "vue-router";
import Dashbord from "../views/Dashbord.vue";
import Produit from "../views/Produit.vue";
import Achat from "../views/Achat.vue";
import Vente from "../views/Vente.vue";
import PointdeVente from "../views/PointdeVente.vue";
import Trésororie from "../views/Trésororie.vue";
import Paraméters from "../views/Paraméters.vue";
import CodeBar from "../views/CodeBar.vue";
import ProduitsEndommagés from "../views/ProduitsEndommagés.vue";
import Retourachat from "../views/Retourachat.vue";
import Avoirachat from "../views/Avoirachat.vue";
import Bondecommande from "../views/Bondecommande.vue";

const routes = [
  {
    path: "/",
    name: "Dashbord",
    component: Dashbord,
  },
  {
    path: "/Produit",
    name: "Produit",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: Produit,
  },
  {
    path: "/Achat",
    name: "Achat",
    component: Achat,
  },
  {
    path: "/Vente",
    name: "Vente",
    component: Vente,
  },
  {
    path: "/PointdeVente",
    name: "PointdeVente",
    component: PointdeVente,
  },
  {
    path: "/Trésororie",
    name: "Trésororie",
    component: Trésororie,
  },
  {
    path: "/Paraméters",
    name: "Paraméters",
    component: Paraméters,
  },
  {
    path: "/CodeBar",
    name: "CodeBar",
    component: CodeBar,
  },
  {
    path: "/ProduitsEndommagés",
    name: "ProduitsEndommagés",
    component: ProduitsEndommagés,
  },
  {
    path: "/Retourachat",
    name: "Retourachat",
    component: Retourachat,
  },
  {
    path: "/Avoirachat",
    name: "Avoirachat",
    component: Avoirachat,
  },
  {
    path: "/Bondecommande",
    name: "Bondecommande",
    component: Bondecommande,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
